.section {
  padding: 60px 0;

  &__title {
    font-size: 50px;
    font-weight: 400;
    line-height: 1.2;
    text-transform: uppercase;
    margin-bottom: 24px;
  }

  @media (max-width: 1199px) {
    &__title {
      font-size: 40px;
    }
  }

  @media (max-width: 991px) {
    &__title {
      font-size: 30px;
      margin-bottom: 20px;
    }
  }
}

.section-3 {
  &__title {
    font-size: 18px;
    font-weight: 400;
    text-transform: uppercase;
    text-align: center;
    border-bottom: 1px solid #333;
    margin-bottom: 20px;
    padding-bottom: 4px;
  }
}

.aside {
  &__title {
    font-size: 14px;
    font-weight: 700;
    line-height: 20px;
    text-transform: uppercase;
    background: $gradient;
    color: $white;
    padding: 10px 16px;
    margin-bottom: 8px;
  }
}
