.header {
  position: relative;
  z-index: 100;
  height: 50px;

  &__wrapper {
    @include shadow("xs");
    width: 100%;
    background-color: $white;
    position: fixed;
    top: 0;
    left: 0;
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 50px;
    position: relative;
  }

  &__logo {
    display: flex;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin-right: auto;

    img {
      height: 40px;
    }
  }

  &.active &__wrapper {
    background-color: $primary;
  }

  &.active &__logo {
    img {
      filter: brightness(0) invert(1);
    }
  }

  @media (min-width: 1200px) {
    height: auto;

    &__wrapper {
      width: auto;
    }

    &__inner {
      width: 65px;
      height: 100vh;
      flex-direction: column;
      padding: 15px 0;
    }

    &__logo {
      width: 100%;
      position: static;
      transform: unset;
      margin-right: 0;
      margin-bottom: auto;

      img {
        width: 100%;
        height: auto;
      }
    }

    &__btn {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}

.lang {
  display: flex;

  &__item {
    display: inline-block;
    font-size: 13px;
    text-transform: uppercase;
    color: #aaa;

    &:hover {
      color: #aaa;
      text-decoration: none;
    }

    &.active {
      color: $danger;
    }

    &:not(:last-child) {
      margin-right: 13px;
      position: relative;

      &::after {
        content: "";
        display: block;
        height: 12px;
        border-right: 1px solid $border-color;
        position: absolute;
        top: 50%;
        right: -7px;
        transform: translateY(-50%);
      }
    }
  }
}

.header.active .lang {
  &__item {
    &.active {
      color: #fff;
    }
  }
}

.cart-btn {
  display: inline-block;
  background: 0;
  border: 0;
  padding: 0;
  font-size: 16px;
  color: #999;
  margin-left: auto;
  margin-right: 10px;

  @media (min-width: 1200px) {
    margin: auto auto 10px auto;
    font-size: 30px;
  }
}

.search-btn {
  display: inline-block;
  background: 0;
  border: 0;
  padding: 0;
  font-size: 16px;
  color: #999;
  margin-right: 10px;

  @media (min-width: 1200px) {
    margin: 0 auto 10px auto;
    font-size: 30px;
  }
}
