.about {
  &__title {
    font-size: 50px;
    font-weight: 400;
    text-transform: uppercase;
    line-height: 1.2;
    margin-bottom: 30px;

    &:not(:first-child) {
      margin-top: 30px;
    }
  }

  &__content {
    font-style: italic;
    font-size: 18px;
    margin-bottom: 60px;
  }

  @media (max-width: 1199px) {
    &__title {
      font-size: 40px;
    }

    &__content {
      font-size: 16px;
      margin-bottom: 40px;
    }
  }

  @media (max-width: 991px) {
    &__title {
      font-size: 30px;
      margin-bottom: 20px;
    }

    &__content {
      font-size: 14px;
    }
  }
}

.news-cat {
  display: flex;
  list-style: none;
  padding-left: 0;
  border-bottom: 1px solid $border-color;
  margin-bottom: 24px;

  &__item {
    &:not(:last-child) {
      margin-right: 30px;
    }
  }

  &__link {
    display: inline-block;
    padding: 8px 0;
    font-size: 14px;
    text-transform: uppercase;
    position: relative;
    color: #333;

    &::after {
      content: "";
      display: none;
      width: 100%;
      height: 3px;
      background: $gradient;
      position: absolute;
      left: 0;
      bottom: -2px;
    }

    &:hover {
      color: $primary;
      text-decoration: none;
    }

    &.active {
      color: $primary;

      &::after {
        display: block;
      }
    }
  }

  @media (max-width: 767px) {
    flex-wrap: wrap;
  }
}

.sharing {
  display: flex;
  align-items: center;
  border-top: 1px solid $border-color;
  border-bottom: 1px solid $border-color;
  padding: 10px 0;

  &__label {
    color: #666;
    margin-right: 20px;
  }

  &__list {
    display: flex;
  }

  &__item {
    display: flex;

    &:hover {
      text-decoration: none;
    }

    &:not(:last-child) {
      margin-right: 16px;
    }

    .fa {
      display: flex;
      width: 35px;
      height: 35px;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      background-color: $facebook;
      color: $white;
    }

    .fa-twitter {
      background-color: $twitter;
    }

    .fa-pinterest {
      background-color: $pinterest;
    }

    .fa-envelope {
      background-color: $dark;
    }

    .fa-print {
      background-color: $light;
      color: #333;
    }
  }
}

.pd-filter {
  background: $gradient;
  padding: 15px;
  border-radius: 4px;
  margin-bottom: 30px;

  .form-control,
  .input-group-text,
  .custom-select {
    border: 0;
  }

  .input-group-text {
    background-color: $white;
  }

  input.form-control {
    padding-left: 16px;
  }

  select.custom-select {
    padding-left: 16px;
  }
}

.pd-cat {
  background-color: $light;

  .nav-item {
    flex: 1;
  }

  .nav-link {
    font-size: 24px;
    line-height: 30px;
    padding: 20px;
    text-align: center;
    color: #333;

    &:hover {
      color: $primary;
      text-decoration: none;
    }

    &.active {
      background: $gradient;
      color: $white;

      &:hover {
        color: $white;
      }
    }
  }

  @media (max-width: 991px) {
    .nav-link {
      font-size: 20px;
      padding: 10px;
    }
  }

  @media (max-width: 767px) {
    display: block;
    white-space: nowrap;
    overflow: auto;

    .nav-link {
      font-size: 16px;
    }
  }
}

.pd-info {
  &__title {
    font-size: 30px;
    font-weight: 700;
    margin-bottom: 8px;
  }

  &__price {
    font-size: 24px;
    margin-bottom: 8px;

    span {
      font-weight: 700;
      color: $danger;
    }

    del {
      color: #999;
      margin-left: 20px;
    }
  }

  &__saving {
    display: inline-block;
    font-size: 14px;
    line-height: 25px;
    padding: 0 6px;
    border-radius: 4px;
    background-color: $danger;
    color: $white;
    margin-left: 20px;
    position: relative;
    top: -10px;
  }

  &__desc {
    margin-bottom: 20px;

    ul {
      padding-left: 17px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__btns {
    display: flex;
    flex-wrap: wrap;
  }

  &__btn {
    display: inline-block;
    flex: 1;
    font-size: 14px;
    line-height: 48px;
    padding: 0 12px;
    border-radius: 4px;
    border: 1px solid $primary;
    color: $danger;
    background-color: rgba($primary, 0.2);
    text-align: center;

    &:hover {
      color: $primary;
      text-decoration: none;
    }

    &:not(:last-child) {
      margin-right: 10px;
    }

    &--primary {
      font-size: 20px;
      font-weight: 700;
      text-transform: uppercase;
      background: $gradient;
      border: 0;
      line-height: 50px;
      color: $white;

      &:hover {
        color: $white;
      }
    }
  }

  @media (max-width: 991px) {
    &__title {
      font-size: 24px;
      margin-bottom: 16px;
    }

    &__price {
      font-size: 20px;
      margin-bottom: 16px;

      del {
        margin-left: 10px;
      }
    }

    &__saving {
      margin-left: 10px;
    }

    &__desc {
      margin-bottom: 16px;
    }

    &__btn {
      line-height: 38px;

      &--primary {
        font-size: 16px;
      }
    }
  }
}

.pd-contact {
  border-top: 1px solid $border-color;
  margin-top: 20px;
  padding-top: 20px;

  &__title {
    font-size: 18px;
    font-weight: 700;
    color: $primary;
    margin-bottom: 8px;
  }

  &__text {
    font-size: 18px;
    font-weight: 700;
    color: $danger;

    a,
    a:hover {
      color: $danger;
    }
  }
}

.detail-tabs {
  margin-bottom: 20px;
}

.h-product {
  position: relative;
  z-index: 2;

  &__content {
    width: 100%;
    background-color: $light;
    padding: 60px 0;
    transform: translateY(-50%);
    position: absolute;
    top: 50%;
    left: 0;
  }

  &__inner {
    width: 36%;
  }

  &__title {
    font-size: 50px;
    line-height: 1.2;
    text-transform: uppercase;
    margin-bottom: 20px;
  }

  &__desc {
    font-size: 16px;
    margin-bottom: 30px;
  }

  @media (max-width: 1199px) {
    padding: 40px 0;

    &__title {
      font-size: 40px;
    }

    &__desc {
      font-size: 14px;
    }
  }

  @media (max-width: 991px) {
    background-color: $light;

    &__content {
      position: static;
      transform: unset;
      padding: 0 0 30px;
    }

    &__inner {
      width: 100%;
    }
  }
}

.distribution {
  padding: 40px 0 60px;

  &__content {
    display: flex;
    flex-direction: column;
    height: 100%;
  }

  &__title {
    font-size: 24px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    color: $primary;
    margin-bottom: 30px;
  }

  &__subtitle {
    font-size: 14px;
    font-weight: 700;
    margin-bottom: 20px;

    img {
      margin-right: 12px;
    }
  }

  &__grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 30px;
  }

  &__body {
    flex: 1;
    position: relative;
  }

  &__list {
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    overflow: auto;
    border-top: 1px solid $border-color;
  }

  &__item {
    border-bottom: 1px solid $border-color;
    padding: 20px 10px 20px 0;

    &:hover {
      background-color: $light;
    }
  }

  &__label {
    display: block;
    font-size: 14px;
    font-weight: 700;
    color: $primary;
    margin-bottom: 8px;
    cursor: pointer;
    padding: 0;
  }

  &__contact {
    padding-left: 24px;
    position: relative;

    &:not(:last-child) {
      margin-bottom: 8px;
    }

    .fa {
      position: absolute;
      top: 4px;
      left: 0;
      color: $primary;
    }
  }

  &__map {
    &::before {
      content: "";
      display: block;
      width: 100%;
      padding-bottom: 133%;
    }
  }

  @media (max-width: 767px) {
    &__grid {
      display: flex;
      flex-direction: column;
    }

    &__left {
      margin-bottom: 30px;
    }

    &__body {
      flex: none;
    }

    &__list {
      position: static;
      height: auto;
      max-height: 300px;
    }
  }
}

.search {
  max-width: 600px;
  margin: 0 auto 60px;

  &__text {
    color: #aaa;
    font-size: 14px;
    margin-top: 4px;
  }

  .input-group {
    border-bottom: 1px solid #999;
    padding-bottom: 4px;
  }

  .form-control {
    border-radius: 0;
    border: 0;
    font-size: 16px;
    padding: 0;
  }

  .input-group-text {
    width: 40px;
    text-align: center;
    justify-content: center;
    border: 0;
    border-radius: 4px !important;
    background: $gradient;
    color: $white;
  }
}
