.footer {
  padding: 40px 0 30px;
  background-color: $light;

  &__grid {
    display: grid;
    grid-template-columns: 2fr 4fr 3fr 3fr;
    grid-gap: 30px;
  }

  &__logo {
    display: inline-flex;

    img {
      height: 82px;
    }
  }

  &__title {
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 16px;

    &:not(:first-child) {
      margin-top: 16px;
    }
  }

  @media (max-width: 1199px) {
    &__grid {
      grid-template-columns: 2fr 4fr 4fr 4fr;
    }
  }

  @media (max-width: 991px) {
    &__grid {
      grid-template-columns: 1fr 1fr;
    }
  }

  @media (max-width: 575px) {
    &__grid {
      grid-template-columns: 1fr;
    }
  }
}

.f-contact {
  list-style: none;
  padding-left: 0;
  margin-bottom: 0;

  &__item {
    &:not(:last-child) {
      margin-bottom: 4px;
    }
  }

  a {
    color: inherit;
    transition: 0.3s;

    &:hover {
      color: $primary;
      text-decoration: none;
    }
  }
}

.f-menu {
  list-style: none;
  padding-left: 0;
  margin-bottom: 0;

  &__item {
    &:not(:last-child) {
      margin-bottom: 5px;
    }
  }

  &__link {
    display: inline-block;
    color: $gray-800;
    transition: 0.3s;
    position: relative;
    padding-left: 20px;
    left: 0;

    &:hover {
      color: $primary;
      text-decoration: none;
      left: 5px;
    }

    &::before {
      content: "\f101";
      font-family: fontawesome;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
}

.social {
  display: flex;

  &__item {
    display: flex;
    width: 35px;
    height: 35px;
    justify-content: center;
    align-items: center;
    background: $gradient;
    border-radius: 8px;
    color: $white;

    &:hover {
      color: $white;
      text-decoration: none;
    }

    &:not(:last-child) {
      margin-right: 10px;
    }
  }
}

.anchor-select {
  background: $gradient;
  color: $white;
  border: 0;
  position: relative;
  cursor: pointer;

  &::after {
    content: "";
    border: 6px solid transparent;
    border-top-color: $white;
    position: absolute;
    top: 50%;
    right: 8px;
    transform: translate(-25%);
  }

  option {
    color: #333;
  }
}
