.news-slider-2 {
  position: relative;
  z-index: 1;
  height: 100%;

  &__container {
    height: 100%;
  }

  &__item {
    height: 100%;
    position: relative;
  }

  &__frame {
    @include frame("rect", 56.25%);
    height: 100%;
  }

  &__content {
    width: 100%;
    background-color: rgba(#fff, 0.8);
    padding: 20px 20px 30px;
    max-width: 500px;
    position: absolute;
    z-index: 9;
    bottom: 0;
    left: 0;
  }

  &__title {
    text-align: center;
    font-size: 18px;
    font-weight: 400;
    margin-bottom: 0;
  }

  &__pagination {
    display: flex;
    justify-content: center;
    width: 100%;
    max-width: 500px;
    position: absolute;
    z-index: 11;
    bottom: 10px;
    left: 0;

    .swiper-pagination-bullet {
      width: 12px;
      height: 12px;
      background: 0;
      border: 1px solid #333;
      opacity: 1;
      margin: 0 4px;

      &:focus {
        outline: 0;
      }

      &-active {
        background-color: #333;
      }
    }
  }

  .swiper-slide {
    height: auto;
  }

  @media (max-width: 767px) {
    &__content {
      padding: 8px 16px 16px;
    }

    &__title {
      font-size: 16px;
    }

    &__pagination {
      bottom: 5px;

      .swiper-pagination-bullet {
        width: 8px;
        height: 8px;
      }
    }
  }
}

.product-slider {
  .swiper-slide {
    padding: 10px;
    height: auto;
  }

  .swiper-slide-active {
    .product-2 {
      background: linear-gradient(to right, #00923f, #85c41e);

      &__icon {
        color: $white;

        img {
          filter: brightness(0) invert(1);
        }
      }

      &__title {
        color: $white;

        a:hover {
          color: $white;
        }
      }

      &__frame {
        display: none;
      }
    }
  }
}
