.post {
  &-title {
    font-size: 50px;
    line-height: 1.2;
    margin-bottom: 16px;
  }

  &-info {
    font-size: 14px;
    color: #999;
    margin-bottom: 24px;
  }

  @media (max-width: 1199px) {
    &-title {
      font-size: 40px;
    }
  }

  @media (max-width: 991px) {
    &-title {
      font-size: 30px;
    }
  }
}

.relate-post {
  &__title {
    font-size: 30px;
    text-transform: uppercase;
    color: $primary;
    border-bottom: 1px solid $border-color;
    padding-bottom: 16px;
    margin-bottom: 30px;
    position: relative;

    &::after {
      content: "";
      display: block;
      width: 100px;
      height: 3px;
      background: $gradient;
      position: absolute;
      bottom: -2px;
      left: 0;
    }
  }

  @media (max-width: 991px) {
    &__title {
      font-size: 24px;
      padding-bottom: 10px;
    }
  }
}
