.menu {
  list-style: none;
  margin: 0;
  padding: 0;

  &-item {
    margin: 4px 0 8px;
    position: relative;
  }

  &-link {
    display: block;
    width: 100%;
    font-size: 14px;
    line-height: 40px;
    padding: 0 20px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    color: inherit;

    &:hover {
      color: inherit;
      text-decoration: none;
    }
  }

  &-toggle {
    display: inline-flex;
    width: 40px;
    height: 40px;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;

    span {
      display: inline-flex;
      width: 24px;
      height: 24px;
      justify-content: center;
      align-items: center;
      border-radius: 12px;
      transition: 0.3s;

      &:hover {
        background-color: rgba(#000, 0.1);
      }
    }

    &.active span {
      transform: rotate(90deg);
    }
  }

  &-sub {
    display: none;
  }

  &-sub &-link {
    padding-left: 40px;
  }

  &-sub-2 &-link {
    padding-left: 60px;
  }

  &-sub-3 &-link {
    padding-left: 80px;
  }

  &-item:hover > &-link {
    background-color: rgba($primary, 0.05);
  }

  &-root {
    color: #999;
  }

  &-root > &-item {
    transform: translateX(-50px);
    opacity: 0;
    transition: all 0.4s ease-out;

    &:nth-child(2) {
      transition-delay: 0.15s;
    }

    &:nth-child(2) {
      transition-delay: 0.3s;
    }

    &:nth-child(3) {
      transition-delay: 0.45s;
    }

    &:nth-child(4) {
      transition-delay: 0.6s;
    }

    &:nth-child(5) {
      transition-delay: 0.75s;
    }

    &:nth-child(6) {
      transition-delay: 0.9s;
    }

    &:nth-child(7) {
      transition-delay: 1.05s;
    }

    &:nth-child(8) {
      transition-delay: 1.2s;
    }

    &:nth-child(9) {
      transition-delay: 1.35s;
    }
  }

  &-root.is-show > &-item {
    opacity: 1;
    transform: translateX(0);
  }

  &-root > &-item > &-link {
    font-size: 24px;

    &.active {
      color: $primary;
    }
  }

  &-root &-sub {
    display: block;
  }

  &-root &-sub &-link {
    display: block;
    line-height: 30px;
  }

  @media (min-width: 1200px) {
    &-root {
      width: 50%;
      max-width: 270px;
    }

    &-root > &-item {
      position: relative;
      z-index: 10;
    }

    &-root &-item:hover > &-link {
      background: 0;
      color: $primary;
    }

    &-root &-sub {
      position: absolute;
      z-index: 9;
      top: -4px;
      left: 100%;
      padding-left: 80px;
    }

    &-root &-sub &-item {
      position: relative;
      transform: translateX(-80px);
      opacity: 0;
      transition: 0s;

      &:first-child {
        &::before {
          content: "";
          display: block;
          width: 80px;
          border-bottom: 1px solid $border-color;
          position: absolute;
          right: 100%;
          top: 21px;
        }
      }
    }

    &-root &-sub &-link {
      font-size: 22px;
      line-height: 40px;
    }

    &-root > &-item:hover &-sub > &-item {
      opacity: 1;
      transform: translateX(0);
      transition: 0.6s ease-out;

      &:nth-child(2) {
        transition-delay: 0.15s;
      }

      &:nth-child(3) {
        transition-delay: 0.3s;
      }

      &:nth-child(4) {
        transition-delay: 0.45s;
      }

      &:nth-child(5) {
        transition-delay: 0.6s;
      }

      &:nth-child(6) {
        transition-delay: 0.75s;
      }

      &:nth-child(7) {
        transition-delay: 0.9s;
      }
    }
  }
}

.menu-aside {
  .menu-item {
    margin: 0;
    border-bottom: 1px solid $border-color;
  }

  .menu-link {
    line-height: 33px;
    padding: 0 6px;
  }
}
