.news {
  &__tag {
    display: inline-block;
    font-size: 16px;
    color: $primary;
    margin-top: 16px;
    margin-bottom: 16px;
  }

  &__title {
    font-size: 16px;
    margin: 24px 0 20px;
  }

  &__tag ~ &__title {
    margin-top: 0;
  }

  @media (max-width: 575px) {
    &__tag {
      font-size: 14px;
      margin-top: 8px;
      margin-bottom: 4px;
    }

    &__title {
      font-size: 14px;
      margin-bottom: 4px;
    }
  }
}

.news-2 {
  position: relative;
  z-index: 1;

  &__frame {
    @include frame("rect", 56.25%);
    @include frame("zoom");
  }

  &__body {
    width: 380px;
    max-width: 100%;
    padding: 20px;
    background-color: rgba(#fff, 0.8);
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 10;
  }

  &__tag {
    display: inline-block;
    font-size: 16px;
    color: $primary;
    margin-bottom: 16px;

    &:hover {
      color: $primary;
      text-decoration: none;
    }
  }

  &__title {
    font-size: 16px;
    margin-bottom: 16px;
  }

  @media (max-width: 767px) {
    &__body {
      padding: 16px;
    }

    &__tag {
      margin-bottom: 8px;
    }

    &__title {
      margin-bottom: 8px;
    }
  }

  @media (max-width: 575px) {
    &__body {
      padding: 8px;
    }

    &__tag {
      font-size: 14px;
      margin-bottom: 4px;
    }

    &__title {
      font-size: 14px;
      margin-bottom: 4px;
    }

    &__time {
      font-size: 12px;
    }
  }
}

.news-3 {
  &__frame {
    @include frame("rect", 62.5%);
    @include frame("zoom");
    width: 120px;
    margin-right: 16px;
  }

  &__title {
    @include max-line(2);
    font-size: 14px;
    margin-bottom: 8px;
  }

  &__time {
    color: #999;
  }
}

.news-4 {
  display: flex;
  flex-direction: column;
  height: 100%;

  &__frame {
    @include frame("rect", 66.66%);
    @include frame("zoom");
    border: 8px solid $border-color;
  }

  &__body {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    padding: 12px;
    border: 1px solid $border-color;
    border-top: 0;
  }

  &__title {
    font-size: 14px;
    margin-bottom: 16px;

    a:hover {
      color: $orange;
    }
  }

  &__time {
    font-size: 14px;
    color: #666;
    margin-top: auto;
    margin-bottom: 8px;
  }

  &__desc {
    @include max-line(3);
  }

  @media (max-width: 767px) {
    &__title {
      font-size: 16px;
    }
  }
}

.article {
  height: 100%;
  padding: 40px 30px 60px;
  border: 1px solid $border-color;

  &__title {
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 36px;
    color: $primary;
    line-height: 1.2;
    min-height: 86px;
    margin-bottom: 24px;
    position: relative;

    &::before {
      content: "";
      display: block;
      width: 2px;
      height: 75px;
      background: linear-gradient(to bottom, #00923f, #85c41e);
      position: absolute;
      top: 50%;
      left: -31px;
      transform: translateY(-50%);
    }
  }

  &__desc {
    @include max-line(4);
    margin-bottom: 24px;
  }

  @media (max-width: 11999px) {
    &__title {
      font-size: 30px;
      min-height: 72px;
    }
  }

  @media (max-width: 767px) {
    padding: 20px;

    &__title {
      font-size: 24px;
      min-height: 56px;
      margin-bottom: 20px;

      &::before {
        height: 56px;
        left: -21px;
      }
    }

    &__desc {
      margin-bottom: 20px;
    }
  }
}

.article-2 {
  &__title {
    font-size: 18px;
    font-weight: 700;
    font-style: italic;
    border-bottom: 2px solid $border-color;
    padding-bottom: 10px;
    margin-bottom: 20px;
    position: relative;

    &::after {
      content: "";
      display: block;
      display: block;
      width: 220px;
      max-width: 100%;
      height: 2px;
      background: $gradient;
      position: absolute;
      bottom: -2px;
      left: 0;
    }
  }

  &__content {
    font-size: 16px;
    margin-top: 24px;
  }
}

.product {
  &__frame {
    display: block;
    border: 2px solid $border-color;
    position: relative;
    overflow: hidden;

    &::before {
      content: "";
      display: block;
      width: 100%;
      padding-bottom: 62%;
    }

    img {
      width: 85%;
      height: 85%;
      object-fit: contain;
      object-position: 50% 50%;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      transition: 0.4s;
    }

    &:hover img {
      transform: translate(-50%, -50%) scale(1.05);
    }
  }

  &__title {
    font-size: 20px;
    margin-top: 12px;
    margin-bottom: 0;
    text-align: center;
  }

  @media (max-width: 991px) {
    &__title {
      font-size: 16px;
      margin-top: 8px;
    }
  }
}

.product-2 {
  height: 100%;
  padding: 40px;
  box-shadow: 0 1px 10px rgba(#000, 0.15);
  background-color: $white;

  &__icon {
    display: flex;
    align-items: flex-end;
    color: $border-color;
    margin-bottom: 30px;

    &::after {
      content: "";
      display: block;
      flex: 1;
      border-bottom: 1px solid currentColor;
    }

    img {
      height: 100px;
    }
  }

  &__title {
    font-size: 30px;
    color: $primary;
    line-height: 1.2;
    min-height: 2.4em;
    margin-bottom: 16px;

    a:hover {
      color: $primary;
    }
  }

  &__frame {
    @include frame("rect", 56.25%);
    @include frame("zoom");
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }

  &__body {
    flex-grow: 1;
    position: relative;
  }

  &__overlay {
    display: flex;
    flex-direction: column;
  }

  &__desc {
    @include max-line(6);
    color: $white;
    margin-bottom: 16px;
  }

  &__btn {
    color: $white;

    &:hover {
      color: $white;
    }

    img {
      filter: brightness(0) invert(1);
    }
  }

  @media (max-width: 1700px) {
    &__icon {
      img {
        height: 90px;
      }
    }

    &__title {
      font-size: 24px;
    }
  }

  @media (max-width: 1399px) {
    padding: 30px;
  }
}
